<template>
 <div>
    <h1>Регистрация</h1>
    <form @submit.prevent="registerVue()">
      <label for="login">Логин</label>
      <div>
          <input id="login" type="text" v-model="newUser.login" required autofocus minlength="6">
      </div>
      <label for="email" >E-Mail</label>
      <div>
          <input id="email" type="email" v-model="newUser.email" required>
      </div>
      <label for="password">Пароль</label>
      <div>
          <input id="password" type="password" v-model="newUser.password" required minlength="6">
      </div>
      <label for="password-confirm">Подтвердите пароль</label>
      <div>
          <input id="password-confirm" type="password" v-model="password_confirmation" required minlength="6">
      </div>
      <div>
          <button type="submit" class="game_button_add">Зарегистрироваться</button>
      </div>
    </form>
 </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex'
export default {
    computed:mapState({
    email:state => state.auth.email,
    //token:state=> state.auth.token
    role:state => state.auth.userRole
  }),
  data(){
      return{
          newUser:{
              email:'',
              login: '',
              password:''
          },
          password_confirmation: ""
      }
  },
  methods:{
    ...mapActions('auth', ['register', 'logout', 'login']),
    ...mapGetters('auth',['isAuthenticated']),
      async registerVue(){
        if (this.newUser.password != this.password_confirmation)
        {
          window.alert("Пароли не совпадают")
          return;
        }
        await this.register(this.newUser);
        this.$router.push('/profile')
      },
      logOut(){
        this.logout()
      }     
  }
}
</script>
<style>
.game_button_add{
  border-radius: 5px;
  text-decoration: none;
  display: inline-block;
  padding: 5px 10px;
  background-image: linear-gradient(45deg, #6ab1d7 0%, #33d9de 50%, #002878 100%);
  background-position: 100% 0;
  background-size: 200% 200%;
  color: white;
  box-shadow: 0 16px 32px 0 rgba(0, 40, 120, .35);
  transition: .5s;
  margin:2px;
}
.game_button_add:hover{
  box-shadow: 0 0 0 0 rgba(0, 40, 120, 0);
  background-position: 0 0;
}
</style>